"use client";

import { useState, useRef } from "react";
import { useQuery, useMutation } from "convex/react";
import { api } from "../../../convex/_generated/api";
import { Id } from "../../../convex/_generated/dataModel";
import Navigation from "@/components/Navigation";
import { useToast } from "@/components/Toast";
import { useConfirm } from "@/components/ConfirmModal";

export default function ContactsPage() {
    const contacts = useQuery(api.contacts.list);
    const createContact = useMutation(api.contacts.create);
    const deleteContact = useMutation(api.contacts.deleteContact);
    const toast = useToast();
    const { confirm, ConfirmDialog } = useConfirm();

    const [showModal, setShowModal] = useState(false);
    const [showImportModal, setShowImportModal] = useState(false);
    const [email, setEmail] = useState("");
    const [firstName, setFirstName] = useState("");
    const [lastName, setLastName] = useState("");
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [importStatus, setImportStatus] = useState<string | null>(null);
    const fileInputRef = useRef<HTMLInputElement>(null);

    const stats = {
        total: contacts?.length ?? 0,
        active: contacts?.filter((c: { status: string }) => c.status === "active").length ?? 0,
        unsubscribed: contacts?.filter((c: { status: string }) => c.status === "unsubscribed").length ?? 0,
        bounced: contacts?.filter((c: { status: string }) => c.status === "bounced").length ?? 0,
    };

    const handleCreate = async () => {
        if (!email.trim()) return;
        setIsSubmitting(true);
        try {
            await createContact({
                email: email.trim().toLowerCase(),
                firstName: firstName.trim() || undefined,
                lastName: lastName.trim() || undefined,
            });
            setShowModal(false);
            resetForm();
            toast.success("Contact added successfully!");
        } catch (error) {
            console.error(error);
            toast.error("Failed to add contact. Email may already exist.");
        } finally {
            setIsSubmitting(false);
        }
    };

    const resetForm = () => {
        setEmail("");
        setFirstName("");
        setLastName("");
    };

    const handleDelete = async (id: Id<"contacts">) => {
        const confirmed = await confirm({
            title: "Delete Contact?",
            message: "This will permanently remove this contact from your list.",
            variant: "danger",
        });
        if (confirmed) {
            try {
                await deleteContact({ id });
                toast.success("Contact deleted");
            } catch {
                toast.error("Failed to delete contact");
            }
        }
    };

    const handleFileUpload = async (e: React.ChangeEvent<HTMLInputElement>) => {
        const file = e.target.files?.[0];
        if (!file) return;

        setImportStatus("Reading file...");
        const text = await file.text();
        const lines = text.split("\n").filter((l) => l.trim());

        if (lines.length < 2) {
            setImportStatus("Error: File must have header and at least one row");
            return;
        }

        const header = lines[0].toLowerCase();
        const hasEmail = header.includes("email");

        if (!hasEmail) {
            setImportStatus("Error: CSV must have an 'email' column");
            return;
        }

        const columns = lines[0].split(",").map((c) => c.trim().toLowerCase());
        const emailIdx = columns.indexOf("email");
        const firstNameIdx = columns.findIndex((c) => c.includes("first"));
        const lastNameIdx = columns.findIndex((c) => c.includes("last"));

        let imported = 0;
        let skipped = 0;

        for (let i = 1; i < lines.length; i++) {
            const row = lines[i].split(",").map((c) => c.trim());
            const email = row[emailIdx];

            if (!email || !email.includes("@")) {
                skipped++;
                continue;
            }

            try {
                await createContact({
                    email: email.toLowerCase(),
                    firstName: firstNameIdx >= 0 ? row[firstNameIdx] : undefined,
                    lastName: lastNameIdx >= 0 ? row[lastNameIdx] : undefined,
                });
                imported++;
            } catch {
                skipped++;
            }
            setImportStatus(`Importing... ${imported} added, ${skipped} skipped`);
        }

        setImportStatus(`Done! ${imported} contacts imported, ${skipped} skipped`);
        if (imported > 0) toast.success(`${imported} contacts imported!`);
        if (fileInputRef.current) fileInputRef.current.value = "";
    };

    return (
        <Navigation>
            <div>
                {/* Header */}
                <div style={{ display: "flex", justifyContent: "space-between", alignItems: "flex-start", marginBottom: 32 }}>
                    <div>
                        <h1 className="page-title">Contacts</h1>
                        <p className="page-subtitle">Manage your subscriber list</p>
                    </div>
                    <div style={{ display: "flex", gap: 12 }}>
                        <button className="btn btn-secondary" onClick={() => setShowImportModal(true)}>
                            üì• Import CSV
                        </button>
                        <button className="btn btn-primary" onClick={() => setShowModal(true)}>
                            <span>+</span> Add Contact
                        </button>
                    </div>
                </div>

                {/* Stats */}
                <div className="stats-grid">
                    <div className="stat-card">
                        <div className="stat-label">Total Contacts</div>
                        <div className="stat-value">{stats.total.toLocaleString()}</div>
                    </div>
                    <div className="stat-card">
                        <div className="stat-label">Active</div>
                        <div className="stat-value" style={{ color: "var(--accent-success)" }}>{stats.active.toLocaleString()}</div>
                    </div>
                    <div className="stat-card">
                        <div className="stat-label">Unsubscribed</div>
                        <div className="stat-value" style={{ color: "var(--accent-warning)" }}>{stats.unsubscribed.toLocaleString()}</div>
                    </div>
                    <div className="stat-card">
                        <div className="stat-label">Bounced</div>
                        <div className="stat-value" style={{ color: "var(--accent-danger)" }}>{stats.bounced.toLocaleString()}</div>
                    </div>
                </div>

                {/* Contacts Table */}
                {!contacts ? (
                    <div className="loading">
                        <div className="spinner" />
                    </div>
                ) : contacts.length === 0 ? (
                    <div className="card">
                        <div className="empty-state">
                            <div className="empty-icon">üë•</div>
                            <h3 className="empty-title">No contacts yet</h3>
                            <p className="empty-text">Add contacts manually or import from CSV</p>
                            <div style={{ display: "flex", gap: 12, justifyContent: "center" }}>
                                <button className="btn btn-secondary" onClick={() => setShowImportModal(true)}>
                                    Import CSV
                                </button>
                                <button className="btn btn-primary" onClick={() => setShowModal(true)}>
                                    Add Contact
                                </button>
                            </div>
                        </div>
                    </div>
                ) : (
                    <div className="table-container">
                        <div className="table-scroll" style={{ maxHeight: 600 }}>
                            <table>
                                <thead>
                                    <tr>
                                        <th>Email</th>
                                        <th>Name</th>
                                        <th>Status</th>
                                        <th>Actions</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {contacts.map((contact: { _id: Id<"contacts">; email: string; firstName?: string; lastName?: string; status: string }) => (
                                        <tr key={contact._id}>
                                            <td style={{ fontWeight: 500 }}>{contact.email}</td>
                                            <td style={{ color: "var(--text-secondary)" }}>
                                                {[contact.firstName, contact.lastName].filter(Boolean).join(" ") || "‚Äî"}
                                            </td>
                                            <td>
                                                <span
                                                    className={`badge ${contact.status === "active"
                                                        ? "badge-success"
                                                        : contact.status === "unsubscribed"
                                                            ? "badge-warning"
                                                            : contact.status === "bounced"
                                                                ? "badge-danger"
                                                                : "badge-neutral"
                                                        }`}
                                                >
                                                    {contact.status}
                                                </span>
                                            </td>
                                            <td>
                                                <button
                                                    className="btn btn-ghost btn-sm"
                                                    onClick={() => handleDelete(contact._id)}
                                                    style={{ color: "var(--accent-danger)" }}
                                                >
                                                    Delete
                                                </button>
                                            </td>
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                        </div>
                    </div>
                )}

                {/* Add Contact Modal */}
                {showModal && (
                    <div className="modal-overlay" onClick={(e) => e.target === e.currentTarget && setShowModal(false)}>
                        <div className="modal" style={{ maxWidth: 480 }}>
                            <div className="modal-header">
                                <h2 className="modal-title">Add Contact</h2>
                                <button className="modal-close" onClick={() => setShowModal(false)}>√ó</button>
                            </div>

                            <div className="modal-body">
                                <div className="form-group">
                                    <label className="form-label">Email Address *</label>
                                    <input
                                        type="email"
                                        className="form-input"
                                        placeholder="email@example.com"
                                        value={email}
                                        onChange={(e) => setEmail(e.target.value)}
                                    />
                                </div>

                                <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gap: 16 }}>
                                    <div className="form-group">
                                        <label className="form-label">First Name</label>
                                        <input
                                            type="text"
                                            className="form-input"
                                            placeholder="John"
                                            value={firstName}
                                            onChange={(e) => setFirstName(e.target.value)}
                                        />
                                    </div>
                                    <div className="form-group">
                                        <label className="form-label">Last Name</label>
                                        <input
                                            type="text"
                                            className="form-input"
                                            placeholder="Doe"
                                            value={lastName}
                                            onChange={(e) => setLastName(e.target.value)}
                                        />
                                    </div>
                                </div>
                            </div>

                            <div className="modal-footer">
                                <button className="btn btn-secondary" onClick={() => setShowModal(false)}>
                                    Cancel
                                </button>
                                <button
                                    className="btn btn-primary"
                                    onClick={handleCreate}
                                    disabled={!email.trim() || isSubmitting}
                                >
                                    {isSubmitting ? "Adding..." : "Add Contact"}
                                </button>
                            </div>
                        </div>
                    </div>
                )}

                {/* Import Modal */}
                {showImportModal && (
                    <div className="modal-overlay" onClick={(e) => e.target === e.currentTarget && setShowImportModal(false)}>
                        <div className="modal" style={{ maxWidth: 520 }}>
                            <div className="modal-header">
                                <h2 className="modal-title">Import Contacts</h2>
                                <button className="modal-close" onClick={() => { setShowImportModal(false); setImportStatus(null); }}>√ó</button>
                            </div>

                            <div className="modal-body">
                                <div
                                    style={{
                                        border: "2px dashed var(--border-light)",
                                        borderRadius: "var(--radius-lg)",
                                        padding: 40,
                                        textAlign: "center",
                                        marginBottom: 20,
                                        cursor: "pointer",
                                        transition: "all var(--transition-fast)",
                                    }}
                                    onClick={() => fileInputRef.current?.click()}
                                    onDragOver={(e) => e.preventDefault()}
                                >
                                    <div style={{ fontSize: 36, marginBottom: 12 }}>üìÑ</div>
                                    <div style={{ fontWeight: 500, marginBottom: 4 }}>Drop CSV file or click to browse</div>
                                    <div style={{ fontSize: 13, color: "var(--text-muted)" }}>Maximum 10,000 contacts per import</div>
                                    <input
                                        ref={fileInputRef}
                                        type="file"
                                        accept=".csv"
                                        style={{ display: "none" }}
                                        onChange={handleFileUpload}
                                    />
                                </div>

                                {importStatus && (
                                    <div
                                        style={{
                                            background: importStatus.includes("Error") ? "rgba(239, 68, 68, 0.1)" : "rgba(16, 185, 129, 0.1)",
                                            padding: 16,
                                            borderRadius: "var(--radius-md)",
                                            color: importStatus.includes("Error") ? "var(--accent-danger)" : "var(--accent-success)",
                                            fontWeight: 500,
                                        }}
                                    >
                                        {importStatus}
                                    </div>
                                )}

                                <div style={{ background: "var(--bg-tertiary)", borderRadius: "var(--radius-md)", padding: 16, marginTop: 16 }}>
                                    <div style={{ fontWeight: 500, marginBottom: 8, fontSize: 14 }}>CSV Format</div>
                                    <code style={{ fontSize: 12, color: "var(--text-secondary)", display: "block" }}>
                                        email,first_name,last_name<br />
                                        john@example.com,John,Doe<br />
                                        jane@example.com,Jane,Smith
                                    </code>
                                </div>
                            </div>

                            <div className="modal-footer">
                                <button className="btn btn-secondary" onClick={() => { setShowImportModal(false); setImportStatus(null); }}>
                                    Close
                                </button>
                            </div>
                        </div>
                    </div>
                )}
            </div>
            <ConfirmDialog />
        </Navigation>
    );
}
